html {
  background-color: #282c34;
}

.App {
  /*text-align: center;*/
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

pre {
  white-space: pre-wrap;       /* css-3 */
  white-space: -moz-pre-wrap;  /* Mozilla */
  white-space: -o-pre-wrap;    /* Opera 7 */
  word-wrap: break-word;       /* Internet Explorer 5.5+ */
}

.App-header {
  display: flex;
  flex-direction: column;
  font-size: calc(5px + 2vmin);
  color: white;
  width: 100vw;
}

.App-link {
  color: #61dafb;
}
